.modal {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	background-color: #00000055;
	z-index: 10;
}

.shoppingCart {
	width: 60%;
	height: 90%;
	margin: 2rem auto;
	background-color: #eee;
	padding: 1rem;
	display: flex;
	flex-direction: column;
	position: relative;
}

.checkout {
	margin: 2rem auto;
	background-color: #868686;
	padding: 1rem;
	display: flex;
	flex-direction: column;
	position: relative;
}

.shoppingCart-checkout {
	position: fixed;
	flex-direction: column;
	top: 0;
	float: left;
	left: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: top;
	text-align: left;
	z-index: 9999;
	font-size: 18px;
	border-radius: 5px;
	background-color: #dadada;
	margin-left: auto;
	display: center; 
	margin-left: auto;
	margin-right: auto;
	border-style: solid;
  }

.shoppingCart .header {
	display: flex;
	background-color: #ccc;
	padding: 10px 20px;
	justify-content: space-between;
}

.close-btn {
	color: var(--main-color); 
}

.empty-text {
	display: block;
	padding: 2rem;
	margin: auto;
}

.cart-products {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding: 1rem 0;
	overflow-y: scroll;
}

.cart-product {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	background-color: #fff;
	padding: 10px 20px;
	border: 3px solid #eee;
}

.product-info {
	flex-basis: 50%;
}

.product-info h3 {
	font-weight: 400;
	font-size: 1rem;
}

.cart-product select {
	width: 70px;
	font-size: 1rem;
	outline: none;
}

.cart-products img {
	width: 100px;
	background-color: #fff;
}

.checkout-btn {
	background-color: var(--main-color);
	padding: 10px 20px;
	font-size: 1rem;
	color: #fff;
	align-self: center;
	margin: 2rem 0;
}

.confirm-btn {
	font-size: 1rem;
	color: rgb(0, 0, 0);
	background-color: var(--main-color);
	align-self: left;
	margin: 2rem 0;
	width: 150px;
	height: 40px;
}
.checkout-window {
	margin-left: 30%;
}
